#navbar {
  background-color: transparent;
  height: var(--menu-height);
  padding: 1rem 0;
  position: fixed;
  z-index: 999;
  width: 100%;
  transition: all .3s ease;

  &.bg--white {
    background-color: var(--blue);
  }

  .navbar-container {
    height: 100%;
    color: var(--white);
    display: flex;
    justify-content: space-between;
    align-items: center;

    .navbar-logo {
      height: 100%;
  
      img {
        height: 100%;
      }
    }

    .nav-menu {
      background-color: var(--orange);
      transform: translateX(100%);
      transition: all .5s;
      position: absolute;
      height: 100vh;
      top: 0;
      right: 0;
      padding: 2rem;
      min-width: 50%;
      display: grid;
      grid-template-rows: repeat(6, min-content);
      row-gap: 2rem;
      align-content: center;

      &.active {
        transform: none;
      }

      .nav-item {
        list-style: none;
      }
    }

    .menu-icon {
      z-index: 999;
      font-size: 1.25rem;
    }
  }
}